import React, { Component, PropTypes } from 'react';
import './message-box.css';

class MessageBox extends Component {
    constructor(props) {
        super(props)

    }

    render() {
        return (
            <div className="message-bubble">
                <form>
                    <div className="form-group">
                    <input className="message-box" value={this.props.userInput} onChange={(e) => this.props.handleInput(e)} type="text" placeholder="Type a message..." />
                    <button className="message-btn" onClick={(e) => this.props.sendMessage(e)}>
                        <img src="./assets/image/send.png" width={'25px'} alt="send icon" />
                    </button>
                    </div>
                </form>
            </div>
        )
    }
}

MessageBox.propTypes = {

}

export default MessageBox


