import React, { Component } from 'react';

import './chat-bubble.css';

class ChatBubble extends Component {
    constructor (props) {
        super(props);
        this.state = {
            sex:this.props.sex
        };
    }
    
  render () {
    return (
        <React.Fragment>
          <div className="Chat">
        <div>
                    <img className={this.props.sender == 'bot' ? "avater-img avater-img-right" : "avater-img"} src={this.props.sender == 'bot'? "./assets/image/bot.png":this.state.sex == 'male' ? "./assets/image/male.png" : "./assets/image/female.png" }/>
        </div>
                <div className={this.props.sender === 'bot' ? "chat-bubble chat-bubble-bot" : this.state.sex === 'male' ? "chat-bubble chat-bubble-male" :"chat-bubble chat-bubble-female"}>
            <p>{this.props.children}</p>
        </div>
        </div>
        </React.Fragment>
    )
  }
}

export default ChatBubble;



