import React, { Component } from 'react'
import logo from './logo.svg'
import './App.css'
import ChatBubble from './components/chat-bubble/chat-bubble';
import MessageBox from './components/message-box/message-box';
import axios from 'axios';
class App extends Component {
constructor (props) {
    super(props)

    this.chatInputHandler = this.chatInputHandler.bind(this)
    this.addMessageToState = this.addMessageToState.bind(this)
    this.sendMessageHandler = this.sendMessageHandler.bind(this)
    // this.getHotelsNearby= this.getHotelsNearby.bind(this)
 

    this.state = {
      userInput: '',
      typing: false,
      location:{
        latitude:'',
        longitude:''
      },
    	messages: [
        {
          sender: 'bot',
          text: 'Hello there!',
          time: Date.now(),
          sex: 'female'
        },
        {
          sender: 'bot',
          text: `I am CodeArcher's Bot.`,
          time: '21:30PM',
          sex: 'female'
        },
        {
          sender: 'user',
          text: `I am A User`,
          time: '21:30PM',
          sex: 'female'
        }
      ]
    }
  }
  //= ======================================== Custom Functions and Event Handler

  chatInputHandler (e) {
    this.setState({
      userInput: e.target.value
    })
  }
  


  // send message to bot and receive response
  sendMessageHandler (e) {
    e.preventDefault()
    const { userInput } = this.state
    var ctime = new Date()
    this.addMessageToState({
      text: userInput,
      sender: 'user',
      time: ctime.getHours() + ':' + ctime.getMinutes()

    })

    this.setState({
      typing: true,
      userInput: ''
    })
let messageType = 'message';
    if(this.state.userInput.includes('train')){
      messageType = 'train'
    } else if (this.state.userInput.includes('version')){
      messageType = 'version'
    }
  
    axios.post(`http://localhost:8000`,{
      type: messageType,question:this.state.userInput
    }).then((response) => {
      const message = response.data.res;
  
      if (message) {
        this.addMessageToState({
          text: message,
          sender: 'bot',
          time: ctime.getHours() + ':' + ctime.getMinutes()
        }, { typing: false },{userInput:''})
      }
    })
      .catch((error) => {
        console.log(error)
        this.setState({
          typing: false,
          userInput:''
        })
      })
  }

  // Add new message to state
  addMessageToState (message, options = { typing: true }) {
    const { messages } = this.state
    this.setState({
      messages: [
        ...messages,
        message
      ],
      typing: options.typing
    })
  }

  componentDidUpdate(){
    window.scrollTo(0, document.querySelector("#chatBox").scrollHeight);

  }
  render () {
    return (
      <div className='App'>
       
        <div className="container-fluid ">
        <div className="row " >
            <div className="col-md pt-5 pb-5 d-none d-md-block" style={{ position: 'fixed', width: '50%', height:'-webkit-fill-available', background:'#0A62C7'}}>
              <div>
              <img src="./assets/image/bot.png" className="img-thumbnail" style={{borderRadius:'50%'}}/>
              <h3 style={{color:'white',padding:'50px'}}>Hi, I am codearcher’s Assistant i will be talking with you while he is out. Hope you like me :)</h3>
            </div>
            </div>

        <div className="col-md-6 push-md-6">
        <div id="chatBox" className=" pr-5 mt-5">
     {this.state.messages.map((d,i)=>{
       return (<ChatBubble  key={i} sex={d.sex} sender={d.sender}>
          {d.text}
        </ChatBubble>)
     })}
        </div>
              <MessageBox userInput={this.state.userInput} handleInput={this.chatInputHandler} sendMessage={this.sendMessageHandler} />
        </div>
      </div></div>
    </div>
    )
  }
}

export default App



